<template>
    <div id="dashboard">
       <ul class="collection with-header">
           <li class="collection-header"><h4>Products</h4></li>
            <li class="collection-header">  ID              name  Quantity         size</li>
            <li v-for="product in products"  v-bind:key="product.id" class="collection-item">
               {{product.product_id}} {{product.name}}  {{product.Quantity}} {{product.size}}

              <router-link class="secondary-content" v-bind:to="{name:'view-product',params:{product_id: product.product_id}}">
                  <i class="fa fa-eye"></i>
              </router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to='/new' class="btn-floating btn-large red">
            <i class="fa fa-plus"></i>
            </router-link>
        </div> 
    </div> 
</template>

<script>
import db from './firebaseInit'
export default {
    name:'dashboard',
    data(){
        return{
             products:[] 
        }
    },
    created ()  {
      db.collection('product').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
              console.log(doc.data())
              const data  = {
               'id': doc.id,
               'product_id': doc.data().product_id,
               'name': doc.data().name,
               'size': doc.data().size,
               'Quantity':doc.data().Quantity
              }
              this.products.push(data)
          })
      }
      )
    }

}
</script>


